import React, { Component } from 'react';

import { connect } from 'react-redux';

import './Color.css';

import colorMap from '../../../image/color-map.png';

import {
  changeColorFrame,
  changeCell,
  changeColorPrimary,
  changeColorSecondary
} from '../../../actions/frameActions';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  changeColorFrame: () => dispatch(changeColorFrame()),
  changeCell: (coordinates) => dispatch(changeCell(coordinates)),
  changeColorPrimary: (color) => dispatch(changeColorPrimary(color)),
  changeColorSecondary: (color) => dispatch(changeColorSecondary(color)),
});

class Color extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ctx: '',
    };
  }

  componentDidMount() {
    this.updateCanvas();
  }

  componentDidUpdate(prevProps, prevState) {
    if (!prevState.ctx) {
      this.updateCanvas();
    }
  }

  async updateCanvas() {
    const ctx = this.refs.canvasColor.getContext('2d');
    const img = new Image();
    img.src = colorMap;
    await ctx.drawImage(img, 5, 0);
    this.setState({ ctx });
  }

  rgbToHex(r, g, b) {
    return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
  }

  handleFrame(event) {
    const { ctx } = this.state
    const rect = ctx.canvas.getBoundingClientRect();
    const x = event.clientX - rect.left;
    const y = event.clientY - rect.top;
    const img_data = ctx.getImageData(x, y, 1, 1).data;
    const R = img_data[0];
    const G = img_data[1];
    const B = img_data[2];
    const color = this.rgbToHex(R, G, B);
    if (color !== '#000000' && this.props.position === 'primary') {
      this.props.changeColorPrimary(color);
    } else if (color !== '#000000' && this.props.position === 'secondary') {
      this.props.changeColorSecondary(color);
    }
  }

  render() {
    return (
      <section className="color_place">
        <canvas
          onClick={this.handleFrame.bind(this)}
          ref="canvasColor" width="300" height="200" id="canvas_picker">
        </canvas>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Color);
