import React, { Component } from 'react';

import { connect } from 'react-redux';

import './Frames.css';

import Frame from './Frame';

import {
  addFrame,
  deleteFrame,
  changeNumber,
  copyFrame,
  changeBorder,
  deleteBorder,
  changeLocation,
} from '../../../../actions/frameActions';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  addFrame: () => dispatch(addFrame()),
  deleteFrame: (numberDelete) => dispatch(deleteFrame(numberDelete)),
  changeNumber: (number) => dispatch(changeNumber(number)),
  copyFrame: (numberFrame) => dispatch(copyFrame(numberFrame)),
  changeBorder: (index) => dispatch(changeBorder(index)),
  deleteBorder: () => dispatch(deleteBorder()),
  changeLocation: (oldPlace, newPlace) => dispatch(changeLocation(oldPlace, newPlace)),
});

class Frames extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fieldsCount: 1,
      onMouseDown: false,
      frame: '',
      dataIndex: '',
      top: '',
    };
  }

  componentDidUpdate(prevProps) {
    const checkFrame = (lastProps, newProps) => {
      if (lastProps.length !== newProps.length) return true;
      if (lastProps[0].length !== newProps[0].length) return true;
      return false;
    }

    if (checkFrame(prevProps.frames, this.props.frames)) {
      this.setState({ fieldsCount: this.props.frames.length });
    }
  }

  addFrame() {
    this.setState({ fieldsCount: this.state.fieldsCount + 1 });
    this.props.addFrame();
  }

  deleteFrame(dataIndex, e) {
    if (this.state.fieldsCount > 1) {
      this.props.deleteFrame(dataIndex);
      this.props.changeNumber(0);
      this.setState({ fieldsCount: this.state.fieldsCount - 1 });
      this.props.deleteBorder();
      this.props.changeBorder(0);
      e.stopPropagation();
    }
  }

  duplicateFrame(dataIndex, e) {
    this.setState({ fieldsCount: this.state.fieldsCount + 1 });
    this.props.copyFrame(dataIndex);
    e.stopPropagation();
  }

  getFrameBorder(index, event) {
    this.props.changeBorder(index);
    event.stopPropagation();
  }

  handleFramesChange(coordinates) { }
  handleCellChange() { }

  takeFrame(dataIndex, event) {
    event.preventDefault();
    const frame = event.target.parentElement.parentElement;
    this.setState({ onMouseDown: true, frame, dataIndex });
    frame.style.position = 'relative';
    frame.style.zIndex = 999;
    const rectCoverFrame = this.refs.frames.getBoundingClientRect();
    const top = event.clientY - rectCoverFrame.top;
    this.setState({ top });
    event.stopPropagation();
  }

  dragFrame(event) {
    event.preventDefault();
    if (this.state.onMouseDown && this.state.frame) {
      const { frame } = this.state;
      const rectCoverFrame = this.refs.frames.getBoundingClientRect();
      const top = event.clientY - rectCoverFrame.top;
      const heightCoverFrame = rectCoverFrame.height;
      const rectFrame = frame.getBoundingClientRect();
      const heightFrame = rectFrame.height;
      let newTop = top - this.state.top;
      const countBack = (Math.floor(heightCoverFrame / heightFrame) - this.state.dataIndex - 1);
      if (newTop < -heightFrame * this.state.dataIndex - 10 * (this.state.dataIndex)) {
        newTop = -heightFrame * this.state.dataIndex - 10 * (this.state.dataIndex);
      }
      if (newTop > countBack * heightFrame + 10 * (countBack)) {
        newTop = countBack * heightFrame + 10 * (countBack);
      }
      frame.style.top = newTop + 'px';
    }
    event.stopPropagation();
  }

  putFrame(event) {
    event.preventDefault();
    if (this.state.onMouseDown) {
      const frame = this.state.frame;
      const rectCoverFrame = this.refs.frames.getBoundingClientRect();
      const top = event.clientY - rectCoverFrame.top;
      const rectFrame = frame.getBoundingClientRect();
      const heightFrame = rectFrame.height;
      let newPlace = Math.floor(Math.round(top / heightFrame) - 1);
      this.props.changeLocation(this.state.dataIndex, newPlace);
      frame.style.position = 'static';
      frame.style.top = 0 + 'px';
      frame.style.zIndex = 2;
      this.setState({ onMouseDown: false, dataIndex: '' });
      this.forceUpdate();
    }
  }

  render() {
    return (
      <section className="frames">
        <div ref="frames" onMouseMove={this.dragFrame.bind(this)} onMouseUp={this.putFrame.bind(this)}>
          {Array.from({ length: this.state.fieldsCount }, () => null).map((field, i) => (
            <div key={i.toString() + 5} className={this.props.framesBorder[i]} onClick={this.getFrameBorder.bind(this, i)}>
              <div key={i.toString() + 7} className="frame_number">{i + 1}</div>
              <div data-index={i} key={i.toString() + 8} className="frame_delete" onClick={this.deleteFrame.bind(this, i)}><i className="far fa-trash-alt"></i></div>
              <Frame onCellChange={this.handleCellChange.bind(this)} onFramesChange={this.handleFramesChange.bind(this)} index={i} key={i.toString()} />
              <div data-index={i} key={i.toString() + 6} className="frame_duplicate" onClick={this.duplicateFrame.bind(this, i)}><i className="far fa-copy"></i></div>
              <div data-index={i} key={i.toString() + 9} className="frame_drag" onMouseDown={this.takeFrame.bind(this, i)}><i className="fas fa-arrows-alt"></i></div>
            </div>
          ))}
        </div>
        <div>
          <button className="frames_button" onClick={this.addFrame.bind(this)} type="button"><i className="fas fa-plus"></i><p>Add new frame</p></button>
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Frames)
