const initialState = {
    number: 0,
    numberСells: 32,
    cell: [],
    button: 0,
    colorfirst: '#e7e7e7',
    colorSecond: '#d2d2d2',
    currentColor: '#f742ee',
    prevColor: 'grey-light',
    primaryColor: '#f742ee',
    secondaryColor: '#ad4646',
    tool: '',
    framesBorder: ["frame_border"],
    frames: [Array.from({ length: 32 }, () => null).map((field, i) => {
        if (i % 2 === 0) {
            return Array.from({ length: 32 }, () => null).map((item, index) => {
                return index % 2 === 0 ? '#e7e7e7' : '#d2d2d2';
            })
        } else {
            return Array.from({ length: 32 }, () => null).map((item, index) => {
                return index % 2 === 0 ? '#d2d2d2' : '#e7e7e7';
            })
        }
    }
    )]
}

export default (state = initialState, action) => {
    switch (action.type) {
        case 'CHANGE_NUMBER':
            return {
                ...state,
                number: action.number
            }

        case 'CHANGE_NUMBERСELLS':
            return {
                ...state,
                numberСells: action.number
            }

        case 'CHANGE_PRIMARY':
            return {
                ...state,
                primaryColor: action.color,
            }

        case 'CHANGE_SECONDARY':
            return {
                ...state,
                secondaryColor: action.color
            }

        case 'CHANGE_CURRENTCOLOR':
            return {
                ...state,
                currentColor: action.color
            }

        case 'CHANGE_PREVCOLOR':
            return {
                ...state,
                prevColor: action.color
            }

        case 'ADD_FRAME':
            const frame = [...state.frames];
            frame.push(Array.from({ length: state.numberСells }, () => null).map((field, i) => {
                if (i % 2 === 0) {
                    return Array.from({ length: state.numberСells }, () => null).map((item, index) => {
                        return index % 2 === 0 ? state.colorfirst : state.colorSecond;
                    })
                } else {
                    return Array.from({ length: state.numberСells }, () => null).map((item, index) => {
                        return index % 2 === 0 ? state.colorSecond : state.colorfirst;
                    })
                }
            }
            ));
            return {
                ...state,
                frames: frame,
                framesBorder: [...state.framesBorder, "frame_border-none"],
            }

        case 'CHANGE_COLORFRAME':
            const newFrameColor = state.frames.map((frame, indexFrame) => {
                if (indexFrame === state.number) {
                    frame = frame.map((row, indexRow) => {
                        return row = row.map((position, indexPosition) => {
                            const chekingCell = state.cell
                                .map((cell) => {
                                    return indexRow === cell.y && indexPosition === cell.x;
                                })
                                .includes(true);
                            if (chekingCell) {
                                return state.button === 0 ? position = state.primaryColor : position = state.secondaryColor;
                            } else {
                                return position;
                            }
                        })
                    });
                }
                return frame;
            });
            return {
                ...state,
                frames: newFrameColor,
                cell: [],
            }

        case 'DELETE_FRAME':
            const newFrames = state.frames.filter((item, indexState) =>
                indexState !== action.numberDelete
            );
            return {
                ...state,
                frames: newFrames,
            }

        case 'DELETE_BORDER':
            const newBorder = state.framesBorder;
            newBorder.pop();
            return {
                ...state,
                framesBorder: newBorder,
            }

        case 'CHANGE_TOOL':
            return {
                ...state,
                tool: action.tool
            }

        case 'COPY_FRAME':
            return {
                ...state,
                frames: [...state.frames, state.frames[action.numberFrame]],
                framesBorder: [...state.framesBorder, "frame_border-none"],
            }

        case 'CHANGE_BORDER':
            return {
                ...state,
                framesBorder: state.framesBorder.map((item, indexState) => {
                    if (indexState === action.index) {
                        return "frame_border";
                    }
                    return "frame_border-none";
                })
            }

        case 'CHANGE_CELL':
            const newCoordinates = state.cell;
            newCoordinates.push(action.coordinates);
            return {
                ...state,
                cell: newCoordinates
            }

        case 'CHANGE_SIZE':
            return {
                ...state,
                numberСells: action.size,
                cell: [],
                frames: Array.from({ length: state.frames.length }, () => null).map(() => {
                    return Array.from({ length: action.size }, () => null).map((field, i) => {
                        if (i % 2 === 0) {
                            return Array.from({ length: action.size }, () => null).map((item, index) => {
                                return index % 2 === 0 ? '#e7e7e7' : '#d2d2d2';
                            })
                        } else {
                            return Array.from({ length: action.size }, () => null).map((item, index) => {
                                return index % 2 === 0 ? '#d2d2d2' : '#e7e7e7';
                            })
                        }
                    }
                    )
                })
            }

        case 'CHANGE_BUTTON':
            return {
                ...state,
                button: action.button
            }

        case 'DELETE_COLORFRAME':
            const oldFrameColor = state.frames.map((frame, indexFrame) => {
                if (indexFrame === state.number) {
                    frame = frame.map((row, indexRow) => {
                        return row = row.map((position, indexPosition) => {
                            const chekingCell = state.cell
                                .map((cell) => {
                                    return indexRow === cell.y && indexPosition === cell.x;
                                })
                                .includes(true);
                            if (chekingCell) {
                                const cheking = ((indexRow % 2 === 0 && indexPosition % 2 === 0) ||
                                    (indexRow % 2 !== 0 && indexPosition % 2 !== 0));
                                return cheking ? position = state.colorfirst : position = state.colorSecond;
                            } else {
                                return position;
                            }
                        })
                    });
                }
                return frame;
            });
            return {
                ...state,
                frames: oldFrameColor,
                cell: [],
            }

        case 'CHANGE_GROUPCELL':
            let newCells = [];
            state.frames.map((frame, indexFrame) => {
                if (indexFrame === state.number) {
                    const { x, y } = action.coordinatesCell;
                    const color = frame[y][x];
                    if (color === state.colorfirst || color === state.colorSecond) {
                        for (let i = x; i < frame.length; i++) {
                            (frame[y][i] === state.colorfirst || frame[y][i] === state.colorSecond) ? newCells.push({ x: i, y: y }) : i = frame.length;
                        }
                        for (let i = x - 1; i > -1; i--) {
                            (frame[y][i] === state.colorfirst || frame[y][i] === state.colorSecond) ? newCells.push({ x: i, y: y }) : i = -1;
                        }

                        newCells.map((cell) => {
                            for (let i = cell.y; i < frame.length; i++) {
                                (frame[i][cell.x] === state.colorfirst || frame[i][cell.x] === state.colorSecond) ? newCells.push({ x: cell.x, y: i }) : i = frame.length;
                            }
                            for (let i = cell.y - 1; i > -1; i--) {
                                (frame[i][cell.x] === state.colorfirst || frame[i][cell.x] === state.colorSecond) ? newCells.push({ x: cell.x, y: i }) : i = -1;
                            }
                            return cell;
                        });
                    } else {
                        for (let i = x; i < frame.length; i++) {
                            frame[y][i] === color ? newCells.push({ x: i, y: y }) : i = frame.length;
                        }
                        for (let i = x - 1; i > -1; i--) {
                            frame[y][i] === color ? newCells.push({ x: i, y: y }) : i = -1;
                        }

                        newCells.map((cell) => {
                            for (let i = cell.y; i < frame.length; i++) {
                                frame[i][cell.x] === color ? newCells.push({ x: cell.x, y: i }) : i = frame.length;
                            }
                            for (let i = cell.y - 1; i > -1; i--) {
                                frame[i][cell.x] === color ? newCells.push({ x: cell.x, y: i }) : i = -1;
                            }
                            return cell;
                        });

                        newCells.map((cell) => {
                            for (let i = cell.x; i < frame.length; i++) {
                                frame[cell.y][i] === color ? newCells.push({ x: i, y: cell.y }) : i = frame.length;
                            }
                            for (let i = cell.x - 1; i > -1; i--) {
                                frame[cell.y][i] === color ? newCells.push({ x: i, y: cell.y }) : i = -1;
                            }
                            return cell;
                        });
                    }

                }
                return frame;
            });
            return {
                ...state,
                cell: newCells,
            }

        case 'CHANGE_COLOR':
            let newColor = '';
            state.frames.map((frame, indexFrame) => {
                if (indexFrame === state.number) {
                    const { x, y } = action.coordinatesCell;
                    newColor = frame[y][x];
                }
                return frame;
            });
            if (state.button === 0) {
                return {
                    ...state,
                    primaryColor: newColor,
                }
            } else {
                return {
                    ...state,
                    secondaryColor: newColor,
                }
            }

        case 'CHANGE_PLACE':
            let { oldPlace, newPlace } = action;
            const frameRelocatable = state.frames[oldPlace];
            const framesRelocatable = state.frames
                .filter((frame, indexFrame) => indexFrame !== oldPlace);
            framesRelocatable.splice(newPlace, 0, frameRelocatable);
            return {
                ...state,
                frames: framesRelocatable
            }

        case 'SET_STATE':
            let { props } = action;
            return props;

        case 'CHANGE_ALLPIXELS':
            const cellPattern = action.coordinatesCell;
            const colorPattern = state.frames[state.number][cellPattern.y][cellPattern.x];

            if (colorPattern === state.colorfirst || colorPattern === state.colorSecond) {
                const framesAllPixelsChange = state.frames.map((frame, indexFrame) => {
                    if (indexFrame === state.number) {
                        frame = frame.map((row, indexRow) => {
                            return row = row.map((position, indexPosition) => {
                                if (state.button === 0) {
                                    return (position === state.colorfirst || position === state.colorSecond) ? position = state.primaryColor : position;
                                } else {
                                    return (position === state.colorfirst || position === state.colorSecond) ? position = state.secondaryColor : position;
                                }
                            })
                        });
                    }
                    return frame;
                });

                return {
                    ...state,
                    frames: framesAllPixelsChange
                }
            } else {
                const framesAllPixelsChange = state.frames.map((frame, indexFrame) => {
                    if (indexFrame === state.number) {
                        frame = frame.map((row, indexRow) => {
                            return row = row.map((position, indexPosition) => {
                                if (state.button === 0) {
                                    return position === colorPattern ? position = state.primaryColor : position;
                                } else {
                                    return position === colorPattern ? position = state.secondaryColor : position;
                                }
                            })
                        });
                    }
                    return frame;
                });

                return {
                    ...state,
                    frames: framesAllPixelsChange
                }
            }

        case 'GET_RECTANGLE':
            const { start, end } = action;
            let x;
            let y;
            let xEnd;
            let yEnd;
            if (start.y < end.y) {
                y = start.y;
                yEnd = end.y;
            } else {
                y = end.y;
                yEnd = start.y;
            }
            if (start.x < end.x) {
                x = start.x;
                xEnd = end.x;
            } else {
                x = end.x;
                xEnd = start.x;
            }
            const framesRect = state.frames.map((frame, indexFrame) => {
                if (indexFrame === state.number) {
                    frame = frame.map((row, indexRow) => {
                        if (indexRow >= y && indexRow <= yEnd) {
                            return row = row.map((position, indexPosition) => {
                                if (indexPosition >= x && indexPosition <= xEnd) {
                                    return state.button === 0 ? position = state.primaryColor : position = state.secondaryColor;
                                } else return position;
                            })
                        } else return row;
                    });
                }
                return frame;
            });

            return {
                ...state,
                frames: framesRect
            }

        case 'GET_RECTANGLEEMPTY':
            {
                const { start, end } = action;
                let x;
                let y;
                let xEnd;
                let yEnd;
                if (start.y < end.y) {
                    y = start.y;
                    yEnd = end.y;
                } else {
                    y = end.y;
                    yEnd = start.y;
                }
                if (start.x < end.x) {
                    x = start.x;
                    xEnd = end.x;
                } else {
                    x = end.x;
                    xEnd = start.x;
                }
                const framesRect = state.frames.map((frame, indexFrame) => {
                    if (indexFrame === state.number) {
                        frame = frame.map((row, indexRow) => {
                            if (indexRow === y || indexRow === yEnd) {
                                return row = row.map((position, indexPosition) => {
                                    if (indexPosition >= x && indexPosition <= xEnd) {
                                        return state.button === 0 ? position = state.primaryColor : position = state.secondaryColor;
                                    } else return position;
                                })
                            } else if (indexRow > y && indexRow < yEnd) {
                                return row = row.map((position, indexPosition) => {
                                    if (indexPosition === x || indexPosition === xEnd) {
                                        return state.button === 0 ? position = state.primaryColor : position = state.secondaryColor;
                                    } else return position;
                                })
                            } else return row;
                        });
                    }
                    return frame;
                });

                return {
                    ...state,
                    frames: framesRect
                }
            }

        case 'GET_LINE':
            {
                const { start, end } = action;
                let x;
                let y;
                let xEnd;
                let yEnd;
                if (start.y < end.y) {
                    y = start.y;
                    yEnd = end.y;
                } else {
                    y = end.y;
                    yEnd = start.y;
                }
                if (start.x < end.x) {
                    x = start.x;
                    xEnd = end.x;
                } else {
                    x = end.x;
                    xEnd = start.x;
                }
                const frame = state.frames[state.number];
                if (yEnd === y) {
                    while (yEnd >= 0 && xEnd >= 0 &&
                        y < state.numberСells && x < state.numberСells &&
                        (xEnd + 1 !== x)) {
                        frame[y][x] = state.button === 0 ? state.primaryColor : state.secondaryColor;
                        x = x + 1;
                    }
                } else if (xEnd === x) {
                    while (yEnd >= 0 && xEnd >= 0 &&
                        y < state.numberСells && x < state.numberСells &&
                        (yEnd + 1 !== y)) {
                        frame[y][x] = state.button === 0 ? state.primaryColor : state.secondaryColor;
                        y = y + 1;
                    }
                } else {
                    while (yEnd >= 0 && xEnd >= 0 &&
                        y < state.numberСells && x < state.numberСells &&
                        (yEnd !== y)) {
                        frame[y][x] = state.button === 0 ? state.primaryColor : state.secondaryColor;
                        y = y + 1;
                        x = x + 1;
                    }
                }
                const framesLine = state.frames.map((fr, indexFrame) => {
                    if (indexFrame === state.number) {
                        return frame;
                    }
                    return fr;
                });

                return {
                    ...state,
                    frames: framesLine
                }
            }

        default:
            return state
    }
}
