import React, { Component } from 'react';

import { connect } from 'react-redux';

import Frame from './frames/Frame';

import './CanvasPlace.css';

import {
  changeColorFrame,
  changeCell,
  deleteColorFrame,
  changeGroupCell,
  changeColor,
  changeAllPixelsSameColor,
  getRectangle,
  getRectangleEmpty,
  getLine,
} from '../../../actions/frameActions';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  changeColorFrame: () => dispatch(changeColorFrame()),
  changeCell: (coordinates) => dispatch(changeCell(coordinates)),
  deleteColorFrame: () => dispatch(deleteColorFrame()),
  changeGroupCell: (coordinatesCell) => dispatch(changeGroupCell(coordinatesCell)),
  changeColor: (coordinatesCell) => dispatch(changeColor(coordinatesCell)),
  changeAllPixelsSameColor: (coordinatesCell) => dispatch(changeAllPixelsSameColor(coordinatesCell)),
  getRectangle: (start, end) => dispatch(getRectangle(start, end)),
  getRectangleEmpty: (start, end) => dispatch(getRectangleEmpty(start, end)),
  getLine: (start, end) => dispatch(getLine(start, end)),
});

class CanvasPlace extends Component {
  constructor(props) {
    super(props);

    this.state = {
      x: '',
      y: '',
      start: '',
    };
  }

  handleFramesChange(coordinates, coordinatesCell, coordinatesCellEnd) {
    if (coordinatesCellEnd) {
      if (this.props.tool === 'Rectangle') {
        this.props.getRectangle(this.state.start, coordinatesCellEnd);
        this.setState({ start: '' });
      } else if (this.props.tool === 'Rectangle Empty') {
        this.props.getRectangleEmpty(this.state.start, coordinatesCellEnd);
        this.setState({ start: '' });
      } else if (this.props.tool === 'Line') {
        this.props.getLine(this.state.start, coordinatesCellEnd);
        this.setState({ start: '' });
      }
    } else {
      if (this.props.tool === 'Pencil') {
        this.props.changeCell(coordinates);
        this.props.changeColorFrame();
      } else if (this.props.tool === 'Eraser') {
        this.props.changeCell(coordinates);
        this.props.deleteColorFrame();
      } else if (this.props.tool === 'Paint bucket' && coordinatesCell) {
        this.props.changeGroupCell(coordinatesCell);
        this.props.changeColorFrame();
      } else if (this.props.tool === 'Choose color' && coordinatesCell) {
        this.props.changeColor(coordinatesCell);
      } else if (this.props.tool === 'Roller' && coordinatesCell) {
        this.props.changeAllPixelsSameColor(coordinatesCell);
      } else if (this.props.tool === 'Rectangle' && coordinatesCell) {
        this.setState({ start: coordinatesCell });
        this.props.changeCell(coordinatesCell);
        this.props.changeColorFrame();
      } else if (this.props.tool === 'Rectangle Empty' && coordinatesCell) {
        this.setState({ start: coordinatesCell });
        this.props.changeCell(coordinatesCell);
        this.props.changeColorFrame();
      } else if (this.props.tool === 'Line' && coordinatesCell) {
        this.setState({ start: coordinatesCell });
        this.props.changeCell(coordinatesCell);
        this.props.changeColorFrame();
      }
    }
  }

  handleCellChange(coordinates) {
    const { x, y } = coordinates;
    if (x >= 0 && y >= 0) {
      this.setState({ x, y });
    }
  }

  clearState() {
    this.setState({ x: '', y: '' });
  }

  render() {
    return (
      <div>
        <section onMouseLeave={this.clearState.bind(this)} className="canvas_place">
          <Frame
            index={this.props.number}
            key={this.props.number}
            onFramesChange={this.handleFramesChange.bind(this)}
            onCellChange={this.handleCellChange.bind(this)}
          />
        </section>
        <section className="info_place">
          <p>[{this.props.numberСells} &#215; {this.props.numberСells}]</p>
          {this.state.x === '' ? (<p></p>) : (<p>{this.state.x} : {this.state.y}</p>)}
        </section>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CanvasPlace);
