import React, { Component } from 'react';
import './App.css';

import Header from './components/screens/main/Header';
import Tool from './components/screens/main/Tool';
import CanvasPlace from './components/screens/main/CanvasPlace';
import Frames from './components/screens/main/frames/Frames';
import Preview from './components/screens/main/preview/Preview';
import LandingPage from './components/screens/LandingPage';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            landingPage: true,
        };
    }

    handleChangePage() {
        this.setState({ landingPage: false });
    }

    render() {
        const { landingPage } = this.state;

        const page = landingPage ? (<div>
            <LandingPage />
            <button type="button" onClick={this.handleChangePage.bind(this)} className="button_editor">Editor</button>
        </div>) : (<div>
            <Header />
            <main>
                <Tool />
                <Frames />
                <CanvasPlace />
                <Preview />
            </main >
        </div>);
        return (
            <div className="App">
                {page}
            </div>
        );
    }
}

export default App;
