import React, { Component } from 'react';

import { connect } from 'react-redux';

import {
  changeTool,
  changeCanvasSize
} from '../../../actions/frameActions';

import Color from './Color';
import './Tool.css';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  changeTool: (tool) => dispatch(changeTool(tool)),
  changeCanvasSize: (size) => dispatch(changeCanvasSize(size)),
});

class Tool extends Component {
  constructor(props) {
    super(props);

    this.state = {
      onOpenColor: false,
    };
  }

  handleChangeTool(tool) {
    this.props.changeTool(tool);
  }

  handleCanvasSize(size) {
    this.props.changeCanvasSize(+size);
  }

  handleChooseColor(position) {
    if (position === "close") {
      this.setState({ onOpenColor: false });
    } else {
      this.setState({ onOpenColor: position });
    }
  }

  render() {
    return (
      <section className="tool">
        <div className="palette">
          <div>
            <div className="pencil" data-title="Pencil" onClick={this.handleChangeTool.bind(this, "Pencil")}>
              <i className="fas fa-pencil-alt"></i>
            </div>

            <div className="paint-bucket" data-title="Paint bucket" onClick={this.handleChangeTool.bind(this, "Paint bucket")}>
              <i className="fas fa-fill-drip"></i>
            </div>
          </div>

          <div>
            <div className="choose-color" data-title="Choose color" onClick={this.handleChangeTool.bind(this, "Choose color")}>
              <i className="fas fa-eye-dropper"></i>
            </div>

            <div className="Eraser" data-title="Eraser" onClick={this.handleChangeTool.bind(this, "Eraser")}>
              <i className="fas fa-eraser"></i>
            </div>
          </div>

          <div>
            <div className="line" data-title="Line" onClick={this.handleChangeTool.bind(this, "Line")}>
              <i className="fas fa-pencil-ruler"></i>
            </div>

            <div className="roller" data-title="Paint all pixels of the same color" onClick={this.handleChangeTool.bind(this, "Roller")}>
              <i className="fas fa-paint-roller"></i>
            </div>
          </div>

          <div>
            <div className="rectangle" data-title="Rectangle" onClick={this.handleChangeTool.bind(this, "Rectangle")}>
              <i className="fas fa-square"></i>
            </div>

            <div className="rectangle" data-title="Rectangle Empty" onClick={this.handleChangeTool.bind(this, "Rectangle Empty")}>
              <i className="far fa-square"></i>
            </div>
          </div>
        </div>

        <div className="canvas-size">
          <h4>Canvas sizes:</h4>
          <p onClick={this.handleCanvasSize.bind(this, "32")}>32 &#215; 32</p>
          <p onClick={this.handleCanvasSize.bind(this, "64")}>64 &#215; 64</p>
          <p onClick={this.handleCanvasSize.bind(this, "128")}>128 &#215; 128</p>
        </div>

        {this.state.onOpenColor ? (
          <div className="colors-part">
            <div
              className="primary_color"
              data-title="Primary color"
              style={{ backgroundColor: this.props.primaryColor }}
              onClick={this.handleChooseColor.bind(this, "primary")}></div>
            <div
              className="secondary_color"
              data-title="Secondary color"
              style={{ backgroundColor: this.props.secondaryColor }}
              onClick={this.handleChooseColor.bind(this, "secondary")}></div>
            <Color position={this.state.onOpenColor} />
            <button onClick={this.handleChooseColor.bind(this, "close")} type="button">Close</button>
          </div>) : (
            <div className="colors-part">
              <div
                className="primary_color"
                data-title="Primary color"
                style={{ backgroundColor: this.props.primaryColor }}
                onClick={this.handleChooseColor.bind(this, "primary")}></div>
              <div
                className="secondary_color"
                data-title="Secondary color"
                style={{ backgroundColor: this.props.secondaryColor }}
                onClick={this.handleChooseColor.bind(this, "secondary")}></div>
            </div>
          )
        }
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Tool);
