import React, { Component } from 'react';

import { connect } from 'react-redux';

import './Preview.css';

import Frame from '../frames/Frame';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({});

class Preview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      counter: 0,
      countFrame: 0,
      interval: null,
      fps: 5
    };
  }

  getNextFrame() {
    this.setState({ countFrame: this.props.frames.length - 1 });
    if (this.state.counter >= this.state.countFrame) {
      this.setState({ counter: 0 });
    } else {
      this.setState({ counter: this.state.counter + 1 });
    }
    setTimeout(() => {
      requestAnimationFrame(this.getNextFrame.bind(this));
    }, 1000 / this.state.fps);
  }

  handleSpeedChange(e) {
    this.setState({
      fps: +e.target.value
    })
  }

  componentDidMount() {
    const intervalId = requestAnimationFrame(this.getNextFrame.bind(this));
    this.setState({
      interval: intervalId,
    });
  }

  componentWillUnmount() {
    clearInterval(this.state.interval);
  }

  handleOpenClick() {
    this.openFullscreen(this.ref);
  }

  handleCloseClick() {
    this.closeFullscreen();
  }

  closeFullscreen() {
    if (document.exitFullscreen) {
      document.exitFullscreen();
    } else if (document.mozCancelFullScreen) {
      document.mozCancelFullScreen();
    } else if (document.webkitExitFullscreen) {
      document.webkitExitFullscreen();
    } else if (document.msExitFullscreen) {
      document.msExitFullscreen();
    }
  }

  openFullscreen(elem) {
    if (elem.requestFullscreen) {
      elem.requestFullscreen();
    } else if (elem.mozRequestFullScreen) { /* Firefox */
      elem.mozRequestFullScreen();
    } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
      elem.webkitRequestFullscreen();
    } else if (elem.msRequestFullscreen) { /* IE/Edge */
      elem.msRequestFullscreen();
    }
  }

  handleFramesChange(coordinates) { }
  handleCellChange() { }

  render() {
    let fields = [];

    for (let i = 0; i < this.props.frames.length; i++) {
      fields.push(<Frame onCellChange={this.handleCellChange.bind(this)} onFramesChange={this.handleFramesChange.bind(this)} index={i} key={i.toString()} />);
    };

    return (
      <section className="preview" ref={(node) => this.ref = node}>
        <div className="preview-area" >
          {fields[this.state.counter]}
        </div>
        <div>
          <input type="range" min="1" step="1" max="24" id="fps" value={this.state.fsp} onChange={this.handleSpeedChange.bind(this)}></input> {this.state.fps}
        </div>
        <h3>FPS</h3>
        <button onClick={this.handleOpenClick.bind(this)} type="button">Open Fullscreen</button>
        <button onClick={this.handleCloseClick.bind(this)} type="button">Close Fullscreen</button>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Preview);
