import React, { Component } from 'react';

import { connect } from 'react-redux';

import './Frame.css';

import { changeNumber, changeCell, changeButton } from '../../../../actions/frameActions';

import drawRect from '../../../../utils/drawRect';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  changeNumber: (number) => dispatch(changeNumber(number)),
  changeCell: (coordinates) => dispatch(changeCell(coordinates)),
  changeButton: (button) => dispatch(changeButton(button)),
});

class Frame extends Component {
  constructor(props) {
    super(props);

    this.state = {
      ctx: '',
      cellSize: null,
      onMouseDown: false,
    };
  }

  handleFrameChange() {
    this.props.changeNumber(this.props.index);
  }

  componentDidMount() {
    this.updateCanvas();
  }

  componentDidUpdate(prevProps) {
    const checkFrame = (lastProps, newProps) => {
      if (lastProps.length !== newProps.length) return true;
      if (lastProps[0].length !== newProps[0].length) return true;
      return (lastProps
        .map((frame, indexFrame) => {
          return (frame
            .map((propsRow, indexPropsRow) => {
              return (propsRow
                .map((propsElement, indexPropsElement) => {
                  return propsElement === newProps[indexFrame][indexPropsRow][indexPropsElement];
                })
                .includes(false));
            })
            .includes(true));
        })
        .includes(true));
    }

    if (checkFrame(prevProps.frames, this.props.frames)) {
      this.updateCanvas();
    }
  }

  updateCanvas() {
    const ctx = this.refs.canvas.getContext('2d');
    this.setState({ ctx });
    this.refs.canvas.width = this.refs.canvas.scrollWidth;
    this.refs.canvas.height = this.refs.canvas.scrollHeight;
    const { numberСells, frames } = this.props;
    const cellSize = this.refs.canvas.scrollWidth / numberСells;
    this.setState({ cellSize });
    const frame = frames[this.props.index];
    for (let j = 0; j < numberСells; j++) {
      for (let i = 0; i < numberСells; i++) {
        const paramForDrawRect = {
          ctx,
          x: i * cellSize,
          y: j * cellSize,
          width: cellSize,
          height: cellSize,
        }
        drawRect(paramForDrawRect, frame[j][i]);
      }
    }
  }

  convertСoordinates(event) {
    const { cellSize } = this.state;
    let x;
    let y;
    if (event.pageX !== undefined && event.pageY !== undefined) {
      x = event.pageX;
      y = event.pageY;
    }
    else {
      x = event.clientX + document.body.scrollLeft +
        document.documentElement.scrollLeft;
      y = event.clientY + document.body.scrollTop +
        document.documentElement.scrollTop;
    }
    x -= this.refs.canvas.offsetLeft;
    y -= this.refs.canvas.offsetTop;
    x = Math.floor(x / cellSize);
    y = Math.floor(y / cellSize);
    return { x, y };
  }

  getCursorPositionStart(event) {
    event.preventDefault();
    this.setState({ onMouseDown: true });
    const coordinates = this.convertСoordinates(event);
    this.props.changeButton(event.button);
    this.props.onFramesChange(coordinates, coordinates);
  }

  getCursorPositionEnd(event) {
    event.preventDefault();
    this.setState({ onMouseDown: false });
    const coordinates = this.convertСoordinates(event);
    this.props.onFramesChange(coordinates,"", coordinates);
  }

  getSegments(event) {
    event.preventDefault();
    const coordinates = this.convertСoordinates(event);
    if (this.state.onMouseDown) {
      this.props.onFramesChange(coordinates);
    }
    this.props.onCellChange(coordinates);
  }

  render() {
    return (
      <section onClick={this.handleFrameChange.bind(this)} className="frame">
        <div className="frame-area">
          <canvas
            ref="canvas"
            onMouseDown={this.getCursorPositionStart.bind(this)}
            onMouseUp={this.getCursorPositionEnd.bind(this)}
            onMouseMove={this.getSegments.bind(this)}
          />
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Frame);
